.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.1s ease-out, visibility 0.1s ease-out;
  overflow: hidden;
}

.overlayVisible {
  display: block;
  visibility: visible;
  opacity: 1;
}
.overlayVisible .drawer {
  transform: translateX(0);
  overflow: auto;
}

.drawer {
  display: flex;
  flex-direction: column;
  overflow: auto;
  position: absolute;
  width: 385px;
  height: 95%;
  right: 0;
  background: #fff;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  padding: 30px;
}
.drawer .items {
  display: flex;
  flex-direction: column;
  overflow: auto;
  margin-bottom: 40px;
}
.drawer h2 {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding-bottom: 30px;
}
.drawer img {
  cursor: pointer;
}

.removBtn {
  position: absolute;
  right: 0;
  width: 12px;
  height: 12px;
  top: 16px;
  right: 15px;
}/*# sourceMappingURL=Drawer.module.css.map */